<script lang="ts">
  import { onMount } from 'svelte'
  import { Button } from 'spaper'
  import { selectedAccount } from '../store'
  import Loader from '../components/Loader.svelte'
  import {
    getPoolCount,
    getUserLPBalance,
    getPendingRewards,
    harvest,
    Address,
    withdraw,
  } from '../helpers/eth-helper'
  import { handleTxWithUx } from '../helpers/tx-helper'
  import { getPantherPrice } from '../helpers/price'

 interface Pool {
   pid: number;
   rewards: number;
   LP: number;
 }

  let pools: Pool[] = []
  let pantherPrice: number
  let loading = true

  onMount(async () => {
    pantherPrice = await getPantherPrice()
    const poolsCount = await getPoolCount()
    selectedAccount.subscribe(async (account) => {
      await getUserPoolInfo(account, poolsCount)
      loading = false
    })
  })

  async function getUserPoolInfo(user: Address, poolsCount: number) {
    for (let i=0; i<poolsCount; i++) {
      pools[i] = {
        pid: i,
        rewards: await getPendingRewards(i, user),
        LP: await getUserLPBalance(i, user),
      }
    }
  }

  async function harvestHandler(pid: number) {
    console.log('harvesting', pid)
    await handleTxWithUx(
      `Harvesting #${pid}`,
      harvest, [pid],
    )
  }

  async function withdrawAllHandler(pid: number) {
    console.log('withdraw all', pid)
    await handleTxWithUx(
      `Withdrawing All from #${pid}`,
      withdraw, [pid, pools[pid].LP]
    )
  }

  $: activePools = pools.filter(pool => pool.LP)
</script>

<h1>farms & pools</h1>
{#if loading}<Loader />{/if}

{#each activePools as { pid, rewards, LP }}
  <div>
    <h4>#{pid}</h4>
    <p class="text-secondary">Staked: {LP.toFixed(3)} LP Token</p>
    <p>Pending Rewards: {Math.floor(rewards)} PANTHER</p>
    <p class="text-success">{(rewards * pantherPrice).toFixed(2)}$</p>

    <Button type="success" on:click={harvestHandler.bind(null, pid)}>
      Harvest
    </Button>
    <Button type="warning" on:click={withdrawAllHandler.bind(null, pid)}>
      Withdraw All
    </Button>
  </div>
{/each}

{#if !loading && !activePools.length}
  <span class="text-warning">
    You have not staked in any farms or pools
  </span>
{/if}
